import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home'
import distlist from '@/components/distlist/distlist'
Vue.use(Router)

export default new Router({
  routes:[
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
		path:'/distlist',
		name:'distlist',
		component:distlist
    }
   
  ]
})



