<template>
	<div class="alist">
		<h6 class="zm">周末去哪</h6>
		<div class="text" v-for="item of alist">
			<img :src="item.src" alt="">
				<h5>{{item.title}}</h5>
				<p>{{item.desc}}</p>
		</div>
	<div class="pri"><b class="iconfont">&#xe629;票面价</b>是指通过景区指定窗口售卖的纸质门票上标注的价格</div>
		
	</div>
</template>
<script>
	export default{
		data(){
			return{
				alist:[
					{
						src:"http://img1.qunarzz.com/sight/source/1505/74/98a840f2a881b.jpg_r_640x214_1bf94c8e.jpg",
						title:"西安温泉季",
						desc:"养生、养性、养心的理想之地，曲径通幽，鸟语花香，让您在远离喧嚣之地享受终南隐士生活。"
					},
					{
						src:"http://img1.qunarzz.com/sight/source/1606/fa/1fca5cab2918f0.jpg_r_640x214_6902e556.jpg",
						title:"咸阳必游TOP10",
						desc:"秦汉文化重要发祥地"
					},
					{
						src:"http://img1.qunarzz.com/sight/source/1506/26/5e8fad4a4a4be2.jpg_r_640x214_8d055589.jpg",
						title:"山外有山",
						desc:"这里曾是太乙真人修炼地，唐王李世民的避暑消夏胜地，山清水秀、景色如画、气候凉爽"
					},
					{
						src:"http://img1.qunarzz.com/sight/source/1511/14/1cf5dc2ecbfc16.jpg_r_640x214_b4b3a58a.jpg",
						title:"醉美丝路行",
						desc:"情系西北，中国梦丝路行"
					},
					{
						src:"http://img1.qunarzz.com/sight/source/1509/1c/6067358591f975.jpg_r_640x214_df0e5390.jpg",
						title:"那些不能忘却的古镇",
						desc:"陕西的丽江，这里有丰富的历史文化资源，古老石砌的巷道，诉说着往日的兴盛与辉煌"
					}
				]
			}
