<style scoped>
	.zm{
		font-size: 0.18rem;
		color:#212121;
		height: 0.4rem;
		line-height: 0.5rem;
		padding-left:0.13rem;
	}
	.text{
		background-color: #fff;
		margin:0.1rem 0;
	}
	.text img{
		width: 100%;
	}
	.text h5{
		font-size: 0.14rem;
		line-height:0.24rem;
		padding-left:0.1rem;

	}
	.text p{
		overflow: hidden;
		font-size: 0.12rem;
		color:#616161;
		line-height: 0.21rem;
		padding-left:0.1rem;
		padding-right:0.75rem;
		white-space:nowrap;
		text-overflow:ellipsis;
	}
	.pri{
		font-size:0.12rem;
		line-height: 0.32rem;
		background-color: #fff;
		/*padding:0.7rem 0.5rem;*/
		padding-left:0.1rem;
		padding-right:0.1rem;
	}
	.pri b{
		color:#616161;
		font-size: 0.12rem;
	}
</style>


