<template>
	<div>
		<haod ></haod>
		<auto-img :bnner="banner"></auto-img>
		<minc :list="pagelist"></minc>
		<coord></coord>
		<htolist :imglist="page"></htolist>
		<page :list="bigimg"></page>
		<sall></sall>
		<food></food>
	</div>
</template>


<script>
	import axios from 'axios';
	import haod from './list/haod';
	import autoImg from './list/autoImg';
	import minc from './list/min'; 
	import coord from './list/coord';
	import htolist from './list/htolist';
	import page from './list/page';
	import sall from './list/sall';
	import food from './list/foot';
	export default {
	 	data(){
	 		return{
	 			page:null,
	 			pagelist:null,
	 			banner:null,
	 			bigimg:null
	 		}
	 	},
	 	components:{
	 		haod,
	 		autoImg,
	 		minc,
	 		coord,
	 		htolist,
	 		page,
	 		sall,
	 		food
	 	},
	 	mounted(){
	 		axios.get("http://yuanyuanyuan.xyz/static/mock/content.json").then((response)=>{
	 			this.page=response.data.imglist;
	 			this.pagelist=response.data.images;
	 			this.banner=response.data.imgbanner;
	 			this.bigimg=response.data.list
	 		})
	 	}
	}
</script>
<style >
	body{
		background-color: #f5f5f5;
	}
	h1,h2,h3,h4,h5,h6{
		font-weight:normal;
	}

</style>


