<template>
	<div>
		<labe :list="prout"></labe>
		
	</div>
</template>
<script>
	import labe from "./list/labe"
	import axios from "axios"
	export default{
		data(){
			return{
				prout:null
			}
		},
		components:{
			labe
		},
		mountex(){
			axios.get("yuanyuanyuan.xyz/static/mock/data.json").then((response)=>{
				this.prout=response.data[this.$route.params.id]
				console.log(1)
			})
		}
	}
</script>


